.cart {
    cursor: pointer;
    position: relative;
        &:after {
            content: "2";
            position: absolute;
            top: -4px;
            right: -2px;
            background: #121212;
            border: 2px solid #fff;
            border-radius: 50%;
            font-size: 9px;
            line-height: 14px;
            font-weight: bold;
            color: #fff;
            width: 16px;
            height: 16px;
            text-align: center;
            vertical-align: middle;
        }
}